import React from "react"
import { useStaticQuery, graphql } from "gatsby"

import mockupDreamWorkImage from "../../images/mockupDreamWork.png"
import dreamWorkFontsImage from "../../images/dreamWork-fonts.png"
import Work from "../../components/work"

export default () => {
  const { dreamWorkImage1, dreamWorkImage2 } = useStaticQuery(graphql`
    query {
      dreamWorkImage1: file(relativePath: { eq: "dreamWorkImage1.png" }) {
        childImageSharp {
          fluid(maxWidth: 1200) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      dreamWorkImage2: file(relativePath: { eq: "dreamWorkImage2.png" }) {
        childImageSharp {
          fluid(maxWidth: 1200) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  return (
    <Work
      title="Dreamwork"
      subTitle="Career Information Website"
      children={
        <div>
          I create a website for posting and seeking jobs in the design field.
          The main color is used based on the 2020 color trend: classic blue. It
          certainly brings a feeling of trust and gentleness for the users who
          are job seekers and recruiters. Tetragons, pentagons, and hexagons are
          used throughout the website to indicate the simple and straightforward
          working style for the company.
        </div>
      }
      mockupImage={mockupDreamWorkImage}
      colors={["#0f4c81", "#cfe1f0", "#d8a405", "#021424", "#ffffff"]}
      fontImage={dreamWorkFontsImage}
      designFluidImages={[
        dreamWorkImage1.childImageSharp.fluid,
        dreamWorkImage2.childImageSharp.fluid,
      ]}
    />
  )
}
